html {
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

body {
    margin: 0;
    font-family: "IBMPlexSans", sans-serif;
    background-color: #ececec;
    font-weight: 400;

}

img {
    max-width: 100%;
    height: auto;
}

a {
    text-decoration: none;
    transition: all .3s;
}



.page {

    min-width: 320px;
    max-width: 1600px;
    box-sizing: border-box;
    margin: 0 auto;
    padding: 0 15px;
    margin-top: 100px;



}



.wrap_title {
    font-size: 70px;
    margin-bottom: 15px;
    color: $black;
    font-weight: 600;

}

.wrap_title {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}

.lk--title {
    font-size: 18px;
    color: $black;
    display: flex;
    align-items: center;


}

.ic_lk--title {
    width: 32px;
    height: 39px;
    margin-right: 5px;
    fill: $black
}

.lk--title {
    transition: none;

    &:hover .ic_lk--title {
        fill: $red;
    }

    &:hover {
        color: $red;
    }
}

@media only screen and (max-width : 1294px) {
    .wrap_title {
        font-size: 60px;
    }
}




@media only screen and (max-width : 1200px) {
    .page {
        padding: 0 0;
        margin-top: 70px;
    }

    .wrap_title {
        display: none;
    }

    body {
        background-color: #fff;
    }

}
